.frontgallery {
    background-color: white;
    height: 500px;
    margin-top: 50px;
}

.frontheader {
    color: black;
    text-align: center;
    margin-bottom: 50px;
    font-size: 50px;
}

.row {
    display: flex;
    flex-wrap: wrap;
    padding: 0 2.5%;
}

.column {
    flex: 25% 1;
    max-width: 20%;
    padding: 0 2.5%;
}

.column img {
    margin: 25px 0;
    vertical-align: middle;
    width: 100%;
}

@media screen and (max-width: 800px) {
    .column {
        flex: 50% 1;
        max-width: 45%;
    }
}

@media screen and (max-width: 600px) {
    .column {
        flex: 100% 1;
        max-width: 95%;
    }
}
.subtext {
    text-align: center;
    font-size: 20px;
}

.frontimg {
    -webkit-transition: -webkit-transform .25s;
    transition: -webkit-transform .25s;
    transition: transform .25s;
    transition: transform .25s, -webkit-transform .25s;
}

.frontimg:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1); 
}

.footer {
    background-color: black;
    margin-bottom: 0px;
    margin-top: 50px;
    padding-top: 20px;
    padding-bottom: 20px;
}

.footer p {
    color: white;
    text-align: center;
    font-size: 20px;
    padding-bottom: 10px;
}
.column button {
    border: 0px solid white;
    background-color: white;
}

.title {
    text-align: center;
    font-size: 50px;
    margin-bottom: 0px;
}

.date {
    text-align: center;
    font-size: 20px;
}

.navbar {
    background-color: white;
}

.el {
    color: black;
    background-color: lightgray;
    border: 0px solid white;
    font-size: 35px;
    width: 50%;
    padding-top: 20px;
    padding-bottom: 20px;
    -webkit-transition: background-color .25s linear;
    transition: background-color .25s linear;
}

.el:hover {
    background-color: lightgray;
}

#select {
    background-color: white;
}

@media screen and (max-width: 800px) {
    .el {
        font-size: 25px;
    }
}

.page {
    margin: 0 0;
    border-top: 2px solid black;
    overflow: hidden;
}

.image {
    float: right;
    width: 45%;
    margin: 50px 2.5%;
}

.text {
    float: left;
    width: 45%;
    margin: 50px 2.5%;
    font-size: 25px;
}

@media screen and (max-width: 1250px) {
    .image {
        float: none;
        width: 95%;
        margin: 20px 2.5%;
    }
    .text {
        float: none;
        width: 95%;
    }
}

.galleryimg {
    -webkit-transition: -webkit-transform .25s;
    transition: -webkit-transform .25s;
    transition: transform .25s;
    transition: transform .25s, -webkit-transform .25s;
}

.galleryimg:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1); 
}

